@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
* {
  font-family: 'Poppins', sans-serif;
}

.App {
  display: flex;
  justify-content: center;
  padding-top: 30px;
}

.RequestInfoBlock {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  min-width: 120px;
  height: 120px;
  padding: 25px;
  border: 1px solid gray;
}

.RequestInfoBlock:nth-child(n + 2) {
  margin-left: 30px;
}

.infoBlock {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  justify-self: start;
  margin-bottom: 20px;
  font-size: 1.2rem;
}

.buttonSuccess, .buttonError {
  padding: 10px 15px;
  border-radius: 15px;
  border: 1px solid gray;
  cursor: pointer;
  font-size: 1rem;
}

